const routes =
    [
        {
            path: '/commodity',
            meta: {
                title: '商品'
            },
            component: (resolve) => require(['../views/Commodity'], resolve)

        },
        {
            path: '/comment',
            meta: {
                title: '评论'
            },
            component: (resolve) => require(['../views/Comment'], resolve)
        },
        {
            path: '/businessman',
            meta: {
                title: '商家'
            },
            component: (resolve) => require(['../views/Businessman'], resolve)
        },
        {
            path: '/',
            redirect: '/commodity'
        }

    ]
export default routes;



