<template>

    <div class="app">
        <div class="index-title">
            <Swiper v-if="productList.seller.avatar.length > 0" :autoPlay='true' :showIndicator='true' interval="1500" duration="1200">
                <Slide v-for="(item,index) in productList.seller.avatar" :key="index">
                   <div class="img-out">
                    <img :src="item.img" alt="">
                   </div>
                </Slide>
            </Swiper>
            <div class="title-info">
                <div class="info-name"><span>品牌</span>{{name}}</div>
                <div class="info-time">{{time}}</div>
                <div class="info-discounts"><span>满</span>{{discounts}}</div>
            </div>
        </div>

        <div class="description-out">
            <div class="description"><span>公告</span>{{description}}</div>
            <div class="arrow"></div>
        </div>
        <div class="navigation">
            <router-link to='/commodity' class="navigation-same navigation-commodity"><span class="pro"
                                                                                            @click="handleThing"
                                                                                            :class="{on:order==='/commodity'}"
            >商品</span>
            </router-link>
            <router-link to="/comment" class="navigation-same navigation-comment"><span class="com" @click="handleCom"
                                                                                        :class="{on:order==='/comment'}">评论</span>
            </router-link>
            <router-link to="/businessman" class="navigation-same navigation-businessman"><span class="buy"
                                                                                                @click="handleBuy"
                                                                                                :class="{on:order==='/businessman'}">商家</span>
            </router-link>
        </div>

        <!--挂载组件-->
        <router-view></router-view>
    </div>
</template>

<script>
    import {Swiper, Slide} from 'vue-swiper-component';

    export default {
        name: 'App',
        data() {
            return {
                order: '/commodity',

            }
        }

        ,components: {
            Swiper,
            Slide
        },
        computed: {

            name() {
                return this.productList.seller.name
            },
            time() {
                return this.productList.seller.description
            },
            discounts() {
                return this.productList.seller.supports[0].description
            },
            description() {
                return this.productList.seller.bulletin
            },
            productList() {
                return this.$store.state.productList
            }},
            methods: {
                handleThing() {

                },
                handleCom() {

                },
                handleBuy() {

                }

            },
            //初始化 加载数据
            mounted() {
                var _this = this;
                this.$store.dispatch('getData')
                this.$router.beforeEach((to, from, next) => {
                    // console.log(this.$route)

                    next();
                    _this.order = to.path;

                })
                // console.log('%%% '+this.previousPath)

                // console.log(this.order+' ####')
            },
            created() {


            }

    }

</script>

<style scoped>
    .app {
        padding: 0px;
        margin: 0px;
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        border: hidden;
        overflow: hidden;
    }

    .title-info {
        position: relative;
        top: -2px;
        display: inline-block;
        margin: 0 0 12px 12px;
        /*background: red;*/
    }

    .index-title {
        width: 100%;
        padding: 15px 15px 0 15px;
    }
.img-out{
    width: 100%;
    height: 240px;
    margin-bottom: 10px;
    overflow: hidden;

}
    .index-title img {
        width: 100%;
        height: 320px;
    }

    .info-name {
        width: 500px;
        height: 30px;
        padding: 0;
        overflow: hidden;
        line-height: 30px;
    }

    .info-name span {
        margin-right: 7px;
        background: red;
        display: inline-block;
        height: 25px;
        line-height: 25px;
        border-radius: 4px;
        color: white;
        font-size: 13px;
        padding: 0px 4px;
        position: relative;
        top: -4px;
    }

    .info-time {
        font-size: 15px;
        padding: 3px 0;
    }

    .info-discounts {
        font-size: 15px;
    }

    .info-discounts span {
        color: red;
        display: inline-block;
        width: 16px;
        height: 16px;
        font-size: 12px;
        text-align: center;
        line-height: 16px;
        border: 1px solid;
        margin-right: 6px;
        position: relative;
        top: -2px;
    }

    .description {
        width: 93%;
        white-space: nowrap;
        text-overflow: ellipsis;
        padding: 8px;
        font-size: 12px;
        overflow: hidden;
        display: inline-block;
    }

    .description span {
        font-size: 12px;
        padding: 1px 3px;
        margin-right: 3px;
        background: white;
        border-radius: 3px;
        color: black;
    }

    .description-out {
        width: 100%;
        height: 31px;
        background: rgba(0, 0, 0, .6);
        color: white;

    }

    .arrow {
        display: inline-block;
        width: 6px;
        height: 6px;
        border: 1px solid #fff;
        border-left-color: transparent;
        border-bottom-color: transparent;
        transform: rotate(45deg);
        position: relative;
        top: -13px;
        right: 5px;
    }

    .navigation {
        /*margin-left: -25px;*/
        width: 100%;
        padding: 6px 0;
        border-bottom: 1px solid #dddee1;

    }

    .navigation-same {
        display: inline-block;
        width: 32%;
        text-align: center;
        text-decoration: none;

    }

    .navigation-same span {
        font-size: 16px;
        color: black;
        /*color: red;*/

    }

    .navigation-same .on {

        color: red;

    }
</style>



