// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import Routes from './router/routes'
import data from './data/data'
// import 'public/style.css'
// import 'swi'

//加载插件
Vue.use(Vuex)
Vue.use(VueRouter)

Vue.config.productionTip = false

const configs = {
    mode: 'hash',
    routes: Routes
}

const router = new VueRouter(configs);

const store = new Vuex.Store({
    state: {
        productList: [],
        cartList: [],
        goods: [],
        foodsArr: []
    },
    //与计算属性类似
    getters: {
        //左边菜系介绍
        foodClassify: state => {
            let foodClassify = state.productList.goods.map(item => item.name)
            return foodClassify
        },
        foodsDetail: (state) => {
            let foodAll = state.productList.goods.map(item => item.foods)
            for (let i = 0; i < foodAll.length; i++) {
                for (let j = 0; j < foodAll[i].length; j++) {
                    Vue.set(foodAll[i][j], 'num', 0)
                }
            }
            return foodAll;
        },
        //评论总数
        commontTotal: state => {
            return state.productList.ratings
        }

    },

    mutations: {
        setProductList(state, data) {

            state.productList = data
            // state.productList.filter(item=>item.push({'count':1}))
        },
        handleReduce(state) {
            return state.count--;
        },
        //    加入购物车
        addCart(getters, name) {
            // getters.allFoods.
        }

    },
    actions: {
        getData(context) {
            setTimeout(() => {
                context.commit('setProductList', data)
            }, 300)
        }
    }
})

// function changeState(state) {
//     // this.state = !state;
//     console.log('啦啦啦啦')
// }

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    render: h => {
        return h(App)
    }

})



